.icon {
  display: inline-block;
  width: 20px;
  height: 20px;
  background-size: 20px 20px;
}
.icon .heart_black {
  background: url(/redux-picSaver/static/media/heart.6c06f3e7.svg);
}
.icon .heart_red {
  background: url(/redux-picSaver/static/media/heart_red.c4cfb5ca.svg);
}
.icon .menu {
  background: url(/redux-picSaver/static/media/menu.14c32a6d.svg);
}
.icon .fire {
  background-image: url(/redux-picSaver/static/media/fire.2eb131d8.svg);
