:root {
  --header_background: #b30016 !important;
}
* {
  margin: 0;
  padding: 0;
}

.event_card {
  position: relative;
  margin-bottom: 10px;
  height: 230px;
  vertical-align: top;
}
.event_card .event_card__image_wrap {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  overflow: hidden;
}
.event_card .event_card__image_wrap .event_card__image {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  background-position: 50%;
  background-size: cover;
}
.event_card .event_card__fade {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  background-color: rgba(0,0,0,0.4);
}
.event_card .event_card__info {
  position: absolute;
  display: flex;
  display: -ms-flexbox;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  box-sizing: border-box;
  padding: 15px;
  width: 100%;
  height: 100%;
  color: #fff;
  font-family: 'FuturaMediumC';
}
.event_card .event_card__info .event_card__title {
  overflow: hidden;
  box-sizing: border-box;
  max-width: 100%;
  max-height: 70px;
  text-shadow: 0 4px 21px rgba(0,0,0,0.5);
  word-wrap: break-word;
  word-break: break-word;
  letter-spacing: 0.012em;
  font-size: 30px;
  line-height: normal;
}
.event_card .event_card__info .event_card__info_item_wrap {
  position: relative;
  overflow: hidden;
  margin-top: 10px;
  padding-top: 5px;
  padding-left: 30px;
  min-height: 24px;
  text-overflow: ellipsis;
  text-shadow: 0 4px 12px rgba(0,0,0,0.5);
  white-space: nowrap;
  font-size: 14px;
  width: 100%;
  box-sizing: border-box;
  color: rgba(255,255,255,0.8);
}
.event_card .event_card__info .event_card__info_item_wrap .Icon {
  position: absolute;
  top: 0;
  left: 0;
}
.event_card .event_card__favorite {
  position: absolute;
  top: 5px;
  right: 0;
  color: #fff;
}

.event_page__wrap .event_page__poster_wrap {
  position: relative;
  top: 0;
  overflow: hidden;
  margin-top: -57px;
  width: 100%;
  height: 230px;
  box-shadow: 0 4px 6px rgba(0,0,0,0.3);
}
.event_page__wrap .event_page__poster_wrap .event_page__image {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  background-position: 50%;
  background-size: cover;
}
.event_page__wrap .event_page__poster_wrap .event_page__fade {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  background-color: rgba(0,0,0,0.4);
}
.event_page__wrap .event_page__info {
  position: absolute;
  top: 0;
  display: flex;
  display: -ms-flexbox;
  flex-direction: column;
  justify-content: flex-end;
  overflow: hidden;
  box-sizing: border-box;
  padding: 15px;
  width: 100%;
  height: 100%;
  color: #fff;
  font-family: 'FuturaMediumC';
}
.event_page__wrap .event_page__info .event_page__title {
  overflow: hidden;
  box-sizing: border-box;
  max-width: 100%;
  max-height: 70px;
  text-shadow: 0 4px 21px rgba(0,0,0,0.2);
  word-wrap: break-word;
  word-break: break-word;
  font-size: 20px;
  line-height: normal;
}
.event_page__wrap .event_page__info .event_page__info_item_wrap {
  position: relative;
  overflow: hidden;
  box-sizing: border-box;
  margin-top: 10px;
  padding-top: 5px;
  padding-left: 30px;
  min-height: 24px;
  width: 100%;
  color: rgba(255,255,255,0.8);
  text-overflow: ellipsis;
  text-shadow: 0 4px 12px rgba(0,0,0,0.4);
  white-space: nowrap;
  font-size: 14px;
}
.event_page__wrap .event_page__info .event_page__info_item_wrap .Icon {
  position: absolute;
  top: 0;
  left: 0;
}
.event_page__wrap .event_page__info .event_page__more {
  margin-top: 14px;
  border: 1px solid #fff;
  color: #fff;
}
.event_page__wrap .event_page__gift_btn {
  display: table;
  margin: 0 auto;
  background-color: #b20016;
  color: #fff;
}

.bg_gift__wrap {
  position: relative;
  overflow: hidden;
  margin-bottom: 10px;
  min-height: 100px;
  background-image: url(/redux-picSaver/static/media/bg-gifts.fb6a511f.jpg);
  background-position: 50%;
  background-size: cover;
}
.bg_gift__wrap:nth-child(1) {
  background-image: url(/redux-picSaver/static/media/bg-gifts-2.f02582ec.jpg);
}
.bg_gift__wrap:nth-child(2) {
  background-image: url(/redux-picSaver/static/media/bg-gifts-3.de5f3086.jpg);
}
.bg_gift__wrap .bg_gift__fade {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: table;
  background-color: rgba(0,0,0,0.5);
}
.bg_gift__wrap .bg_gift__content {
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  padding: 10px;
  background-color: rgba(0,0,0,0.5);
  color: #fff;
}
.bg_gift__wrap .bg_gift__title {
  margin-bottom: 10px;
}
.bg_gift__wrap .bg_gift__balls,
.bg_gift__wrap .bg_gift__timeout {
  display: table;
  margin: 0 auto;
  font-size: 12px;
}
.bg_gift__wrap .bg_gift__get {
  display: table;
  margin: 0 auto;
  margin-top: 14px;
  border: 1px solid #fff;
  color: #fff;
}

.gift_page__header {
  font-size: 28px;
  font-family: FuturaMediumC;
  line-height: normal;
}

